.product-list-dash-container{
    display: flex;
    margin-top: 10px;
}
.productList{
    flex: 4 1;
    margin-top: 10px;
}
.loader-box{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.productListItem{
    display: flex;
    align-items: center;
}

.productListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }

.dashproductListEdit{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #3bb077;
    color: white;
    cursor: pointer;
    margin-right: 20px;
    line-height: 1.5;
}

.productListDelete{
    color: red;
    cursor: pointer;
}

.productAddButton {
    margin: 20px auto;
    padding: 5px 10px;
    border: none;
    color: #fff;
    background: #000;
    border-radius: 5px;
    cursor: pointer;
}
.topbar {
  width: 100%;
  height: 50px;
  background-color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
 
}
.productForm {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.productTopRight{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.topbarWrapper {
  height: 100%;
  width: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 1;
}

.logo {
  font-weight: bold;
  font-size: 30px;
  color: darkblue;
  cursor: pointer;
}

.topRight{
    display: flex;
    align-items: center;
}

.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  color: #555;
}

.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0px;
    background-color: red;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}

@media screen and (max-width: 320px) {
  .topbar {
    width: 100%;
    height: 50px;
    
  }
  .topbarWrapper{
    width: 100% !important;
  }
  .productForm {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.productTopRight{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
}
.sidebar {
  flex: 1 1;
  /* width: 25%; */
  height: calc(90vh - 50px);
  /* height: auto; */
  background-color: rgb(251, 251, 255);
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
  padding-bottom: 20px;
}

.sidebarWrapper {
  padding: 20px;
  color: #555;
}

.sidebarMenu {
  margin-bottom: 10px;
}

.sidebarTitle {
  font-size: 13px;
  color: rgb(187, 186, 186);
}

.sidebarList {
  list-style: none;
  padding: 5px;
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color: rgb(240, 240, 255);
}

.sidebarIcon{
    margin-right: 5px;
    font-size: 20px !important;
}

